@import url(https://fonts.googleapis.com/css?family=Ubuntu:400,700);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: #211a4c;
  background: linear-gradient(135deg, #9d190d 0%, #ee6439 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#9D190D', endColorstr='#EE6439',GradientType=1 );
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: sans-serif;
  font-size: 1em;
  color: #222;
}

#app {
  width: 90%;
  background: white;
  padding: 5px;
  border-radius: 5px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  text-align: center;
}

h1 {
  margin: 0;
  font-size: 5em;
}

.container {
  border: 2px solid #ccc;
  padding: 30px;
  border-radius: 5px;
}

.displayedTime {
  margin-bottom: 30px;
}

button,
input[type="submit"] {
  width: 100%;
  padding: 20px;
  margin-bottom: 5px;
  margin-right: 5px;
  border: none;
  border-radius: 5px;
  background-color: #bbb;
  font-size: 1.3em;
  color: white;
  cursor: pointer;
  transition: color 0.3s, background-color 0.3s;
}

.preset {
  width: 30%;
  padding: 20px;
  margin-bottom: 5px;
  margin-right: 5px;
  border: none;
  border-radius: 5px;
  background-color: #bbb;
  font-size: 1.3em;
  color: white;
  cursor: pointer;
  transition: color 0.3s, background-color 0.3s;
}

button:hover {
  background-color: #df2b2b;
}

input[type="submit"] {
  background-color: #9d190d;
}

input[type="submit"]:hover {
  background-color: #ee6439;
}

input[type="text"] {
  text-align: center;
  margin-bottom: 30px;
  padding: 0;
  height: 45px;
  font-size: 1em;
  width: 100%;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 2px solid #222;
}

@media (min-width: 640px) {
  #app {
    width: 50%;
  }
}

@media (min-width: 900px) {
  h1 {
    font-size: 6em;
  }

  #app {
    width: 35%;
  }
}

